
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'LingPickerTime',
components: {
Group,
XSwitch,
XDialog,
Flexbox,
FlexboxItem,
XHeader,
XButton,
Cell
},
props: {
time: ''
},
data() {
return {
show: false,
nowTime: ''
}
},
computed: {
getYears() {
return this.$store.getters.getYears
},
getMonths() {
return this.$store.getters.getMonths
},
getDays() {
return this.$store.getters.getDays
}
},
methods: {
handleLeaveTimeChange(event) {
console.log(event)
},
/**
* 判断是不是闰年
* @param  {Number}  year 年份
* @return {Boolean}      是否是闰年
*/
isLeapYear (year) {
return year % 100 !== 0 && year % 4 === 0 || year % 400 === 0
},
/**
* 获取某年某月的天数
* @param  {Date} date    日期
* @return {Number}       天数
*/
getMaxDay (date) {
let year = parseFloat(date.getFullYear())
let month = parseFloat(date.getMonth() + 1)
if (month === 2) {
return this.isLeapYear(year) ? 29 : 28
}
return [4, 6, 9, 11].indexOf(month) >= 0 ? 30 : 31
},
getDayArr(date) {
let nowDay = date.getDate()
if(nowDay == 1) {
return ['', 1, 2]
}
if(this.getMaxDay(date) == nowDay) {
return [nowDay-1, nowDay, '']
}
return [nowDay-1, nowDay, nowDay+1]
},
getMonthArr(date) {
let nowMonth = date.getMonth() + 1
if(nowMonth == 1) {
return ['', 1, 2]
}
if(nowMonth == 12) {
return [nowMonth-1, nowMonth, '']
}
return [nowMonth-1, nowMonth, nowMonth+1]
},
getYearArr(date) {
let nowYear = date.getFullYear()
return [nowYear-1, nowYear, nowYear+1]
},
handleYear (options) {
let year = new Date(this.nowTime).getFullYear()
if(options.type == 'swipeup') {
year += Math.floor(Math.abs(options.velocityY*2) + 1)
this.$store.commit('mutationYears', this.getYearArr(new Date(`${year}-1-1`)))
} else if(options.type == 'swipedown') {
year -= Math.floor(Math.abs(options.velocityY*2) + 1)
this.$store.commit('mutationYears', this.getYearArr(new Date(`${year}-1-1`)))
}
},
handleMonth (options) {
let year = new Date(this.nowTime).getFullYear()
let month = new Date(this.nowTime).getMonth() + 1
if(options.type == 'swipeup') {
if( month == 12) { return }
month += Math.floor(Math.abs(options.velocityY*2) + 1)
if( month > 12) { month = 12 }
this.$store.commit('mutationMonths', this.getMonthArr(new Date(`${year}-${month}-1`)))
} else if(options.type == 'swipedown') {
if( month == 1) { return }
month -= Math.floor(Math.abs(options.velocityY*2) + 1)
if(month < 1) { month = 1 }
this.$store.commit('mutationMonths', this.getMonthArr(new Date(`${year}-${month}-1`)))
}
},
handleDay (options) {
let year = new Date(this.nowTime).getFullYear()
let month = new Date(this.nowTime).getMonth() + 1
let day = new Date(this.nowTime).getDate()
if(options.type == 'swipeup') {
let maxDay = this.getMaxDay(new Date(this.nowTime))
if(maxDay == day) { return }
day += Math.floor(Math.abs(options.velocityY*2) + 1)
if(maxDay < day) { day = maxDay }
this.$store.commit('mutationDays', this.getDayArr(new Date(`${year}-${month}-${day}`)))
} else if(options.type == 'swipedown') {
if( day == 1) return ;
day -= Math.floor(Math.abs(options.velocityY*2) + 1)
if(day < 1) {day = 1}
this.$store.commit('mutationDays', this.getDayArr(new Date(`${year}-${month}-${day}`)))
}
},
handleNowTime: function() {
this.$emit('update-time', this.nowTime)
this.show = false
}
},
watch: {
getYears: function() {
this.nowTime = `${this.getYears[1]}-${this.getMonths[1]}-${this.getDays[1]}`
let maxDay = this.getMaxDay(new Date(`${this.getYears[1]}-${this.getMonths[1]}-1`))
if(maxDay < this.getDays[1]) {
this.nowTime = `${this.getYears[1]}-${this.getMonths[1]}-${maxDay}`
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
} else {
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
}
},
getMonths: function() {
this.nowTime = `${this.getYears[1]}-${this.getMonths[1]}-${this.getDays[1]}`
let maxDay = this.getMaxDay(new Date(`${this.getYears[1]}-${this.getMonths[1]}-1`))
if(maxDay < this.getDays[1]) {
this.nowTime = `${this.getYears[1]}-${this.getMonths[1]}-${maxDay}`
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
} else {
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
}
},
getDays: function() {
this.nowTime = `${this.getYears[1]}-${this.getMonths[1]}-${this.getDays[1]}`
}
},
created() {
this.$store.commit('mutationDays', this.getDayArr(new Date()))
this.$store.commit('mutationMonths', this.getMonthArr(new Date()))
this.$store.commit('mutationYears', this.getYearArr(new Date()))
let unwatch = this.$watch('nowTime', function (newVal, oldVal) {
if(oldVal == '' && newVal != '') {
this.$emit('update-time', newVal)
unwatch()
}
})
}
}



